export default {
	name: 'comment-item',
	props: {
		dataList: {
			// 要显示的数组
			type: Array,
			default () {
				return [];
			}
		},
		pageType: {
			type: Number,
			default () {
				return 1;
			}
		},
		nodata: {
			// 是否有数据
			type: Boolean,
			default () {
				return false;
			}
		},
		showNum: {
			// 是否有数据
			type: Boolean,
			default () {
				return false;
			}
		},
		isDetail: {
			// 是否是详情
			type: Boolean,
			default () {
				return false;
			}
		},
		nomore: {
			// 是否有更多数据
			type: Boolean,
			default () {
				return false;
			}
		},
		showOper:{
		  type: Boolean,
		  default(){
		    return false;
		  }
		},
		isRanking:{
		  type: Boolean,
		  default(){
		    return false;
		  }
		},
		hideFootBtn:{ // 隐藏随便看看
		  type: Boolean,
		  default(){
		    return false;
		  }
		},
	},
	data() {
		return {};
	},
	methods: {
		showAll(ind) {
			this.$emit('showAll',ind);
		},
		hideMore(ind) {
			this.$emit('hideMore',ind);
			// this.dataList[ind].showMore = false;
		},
		setImg(src) {
			this.$emit('setImg', src)
		},
		showBigImg(listInd, imgInd) {
			let info = this.dataList[listInd];
			let imgList = info.articleInfo.imgList;
			this.$acFrame.Util.showBigPic(imgList[imgInd], imgList);
			getApp().globalData.isShowPic = true
		},
		loadMoreData() {},
		linkDetail(obj, type) {
			uni.navigateTo({
				url: `/pages/home/commentDetail?data=${encodeURIComponent(JSON.stringify(obj))}&pageType=${this.pageType}&type=${type?type:''}`
			});
		},
		rewardList(id){
			wx.navigateTo()
		},
		linktoshop() {
			// debugger
			// uni.switchTab({
			// 	url:"pages/home/index"
			// })
			this.$parent.cancelSearch()
			// this.$emit('childLink');
		},
		guanzhu(code, ind) {
			let selef = this
			this.$emit('followPost', code, ind)
		},
		linkUser(id) {
			uni.navigateTo({
				url: `/pages/mycenter/mycenter?id=${id}`
			})
		},
		linkTheme(id) {
			this.$acFrame.HttpService.readTopic({id:id}).then(res=>{
				if(res.success){}
			})
			uni.navigateTo({
				url: `/pages/home/topicIndex?id=${id}`
			})
		},
		shopDetail(id){
			uni.navigateTo({
				url:"/pages/myshop/shopDetail?id="+id
			})
		},
		userInfo(userCode) {
			uni.navigateTo({
				url: `/pages/mycenter/mycenter?userCode=${userCode}`
			})
		},
		setErrorPic(ind_p, ind_c) {
			this.dataList[ind_p].articleInfo.imgList[ind_c] = "/images/head1.png"

		},
		dianzan(id, ind) {
			this.$emit('dianzan', id, ind)
		},
		prodDetail(id){
			uni.navigateTo({
				url:"/pages/myshop/productDetail?id="+id
			})
		},
		linkProd(key) {
			let url = ''
			switch (key) {
				case 1:
					url = '/pages/ranking/friends'
					uni.navigateTo({
						url: url
					})
					break;
				case 2:
					url = '/pages/ranking/post'
					uni.navigateTo({
						url: url
					})
					break;
				case 3:
					url = '/pages/ranking/invitation'
					uni.navigateTo({
						url: url
					})
					break;
				case 4:
					url = '/pages/ranking/product'
					uni.navigateTo({
						url: url
					})
					break;
				case 5:
					url = '/pages/ranking/product'
					uni.navigateTo({
						url: url
					})
					break;
				default:
					break;
			}
			// uni.navigateTo({
			// 	url: `/pages/myshop/productDetail?goodsId=${id}`
			// })
		},
		linkRanking(type) {
			let url = ''
			switch (type) {
				case 1:
					url = `/pages/ranking/friends?type=${type}`
					break;
				case 2:
					url = `/pages/ranking/post?type=${type}`
					break;
				case 3:
					url = `/pages/ranking/invitation?type=${type}`
					break;
				case 4:
					url = `/pages/ranking/product?type=${type}`
					break;
				default:
					break;
			}
			uni.navigateTo({
				url: url
			})
		},
		operPost(obj){
			let self = this
			uni.showActionSheet({
				itemList:['查看','删除'],
				itemColor:'#007AFF',
				success:(res)=>{
					if(res.tapIndex==0){
						uni.navigateTo({
							url: `/pages/home/commentDetail?data=${encodeURIComponent(JSON.stringify(obj))}&pageType=${this.pageType}`
						});
					}else{
						self.$acFrame.HttpService.deletePost({id:obj.articleInfo.id}).then(res=>{
							if(res.success){
								self.$acFrame.Util.mytotal("删除成功！")
								setTimeout(function() {
									self.$parent.setParams()
									self.$parent.initData()
								}, 1000);
							}
						})
					}
				}
					
			})
		}
	},
	watch: {}
};
